import * as React from 'react'

export interface IDimension {
    width: number,
    height: number
}

interface ICameraViewState {
    stream: any
}

interface ICameraViewProps {
    dummy?: any
}
class CameraView extends React.Component<ICameraViewProps, ICameraViewState> {

    private videoObject: HTMLVideoElement
    constructor(props: ICameraViewProps) {
        super(props)
        this.state = {
            stream: {}
        }
    }

    public componentDidMount() {
        const constraints = { video: { facingMode: "environment" }, audio: false }
        this.videoObject = document.createElement("video")
        if (navigator.mediaDevices) {
            navigator.mediaDevices
                .getUserMedia(constraints)
                .then( (stream) => {
                    this.videoObject.srcObject = stream
                    this.videoObject.play().then( () => alert('hey thats pretty good'))
                })
                .catch((error) => {
                    alert(error);
                })
        }
    }

    public render() {
        return (
            <div>
                <canvas id="camera--sensor" />
                {this.videoObject}
                <img src="//:0" alt="" id="camera--output" />
                <button id="camera--trigger">Take a picture</button>
            </div>
        )
    }
}

export default CameraView


