import * as React from 'react'
import Viewport from './viewport'

import * as gn from './gyronorm.complete.js'

interface IGyroData {
  dm: {
    x: number
    y: number
    z: number
    gx: number
    gy: number
    gz: number
    alpha: number
    beta: number
    gamma: number
  }
}

interface IAppState{
  xAcceleration: number,
  yAcceleration: number,
  zAcceleration: number  
}

class App extends React.Component<any, IAppState> {

  constructor(props: any) {
    super(props)
    this.state = {
      xAcceleration: 0,
      yAcceleration: 0,
      zAcceleration: 0 
    }

    this.getData = this.getData.bind(this)

    gn.prototype.init().then(() => {
      gn.prototype.start(this.getData)
    }).catch((e: any) => {
      // Catch if the DeviceOrientation or DeviceMotion is not supported by the browser or device
    })
  }

  private getData (data: IGyroData) {
    this.setState({
      xAcceleration: data.dm.x,
      yAcceleration: data.dm.y,
      zAcceleration: data.dm.z
    })
  }

  public render() {
    return (
      <div>
        <Viewport
          xAcceleration={this.state.xAcceleration}
          yAcceleration={this.state.yAcceleration}
          zAcceleration={this.state.zAcceleration}
        />
      </div>
    )
  }
}

export default App;


