import * as React from 'react'
import CameraView from './cameraView';

export interface IDimension {
    width: number,
    height: number
}

interface IViewportState {
    orientation: 'portrait' | 'landscape'
    xVal: number,
    yVal: number,
    zVal: number
}

interface IViewportProps {
    xAcceleration: number,
    yAcceleration: number,
    zAcceleration: number
}
class Viewport extends React.Component<IViewportProps, IViewportState> {

    private xVal: number = 0
    private yVal: number = 0
    private zVal: number = 0

    public render() {
        // const dimensions = this.getDimensions()
        const divStyle: React.CSSProperties = {
            width: '100%',
            textAlign: 'center'
        }
        if (this.props.xAcceleration > this.xVal) {
            this.xVal = this.props.xAcceleration
        }
        if (this.props.yAcceleration > this.yVal) {
            this.yVal = this.props.yAcceleration
        }
        if (this.props.zAcceleration > this.zVal) {
            this.zVal = this.props.zAcceleration
        }
        return (
            <div>
                <div id="viewport" style={divStyle}>
                    xAcceleration: {this.props.xAcceleration.toFixed(2)} <br />
                    yAcceleration: {this.props.yAcceleration.toFixed(2)} <br />
                    zAcceleration: {this.props.zAcceleration.toFixed(2)}
                </div>
                <div>
                    <h3>MAX x: {this.xVal}</h3>
                    <h3>MAX y: {this.yVal}</h3>
                    <h3>MAX z: {this.zVal}</h3>
                </div>
                <CameraView />
            </div>
        )
    }
}

export default Viewport;


